import { EV_REPORT_PROP } from "@/entities/EventReport/type/event-report-type";
import { FC } from "react";
import ALabel from "../Label/ALabel";

interface ATextProps {
    label: string | null,
    height: number,
    nameForHandler: EV_REPORT_PROP.COMMENT,
    handleChange: (
        value: string
    ) => void,

}


const AText: FC<ATextProps> = ({
    label,
    height,
    nameForHandler,
    handleChange,

}) => {
    const id = `input-${nameForHandler}`;
    return (
        <>
            <div className="col-md-12 mb-1">
                {label && <ALabel
                    htmlId={id}
                    label={label}
                    errorMessage={null}

                />}
                <textarea
                    onChange={
                        (e) => handleChange(
                            e.target.value
                        )}
                    // onFocus={handleOnNameFocus}
                    className="form-control mt-2"
                    // type="text"
                    // defaultValue="2019-08-19"
                    id="description-string-input"
                    lang="rus"
                    rows={height}
                />
            </div>

        </>
    );
}

export default AText;