import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { initialEventApp } from "../model/EventThunk";
import { Suspense, useEffect, useState } from "react";
import Preloader from "@/shared/ui/Preloader/Preloader";
import { EventListPage } from "@/pages/event/ListPage";
import EventItemPage from "@/pages/event/ItemPage/ui/EventItemPage";
import { Header } from "@/widgets/Header";




export const EventContainer = () => {
    const dispatch = useAppDispatch();
    const currentPage = useAppSelector(state => state.event.currentPage);
    const isMenuActive = currentPage == 'item';
    const isFinish = useAppSelector(state => state.event.isFinish)

    const initialEventReport = () => {
        dispatch(
            initialEventApp()
        )
    }



    const [isFetched, setIsFetched] = useState(false)
    const [isFinishComponent, setIsFinishComponent] = useState(isFinish)

    useEffect(() => {
        if (!isFetched && !isFinishComponent) {
            initialEventReport()
            setIsFetched(true)

        }

    }, [isFetched, isFinishComponent])

    useEffect(() => {

        if (isFinish) {
            setIsFetched(false)
        }
        setIsFinishComponent(isFinish)

    }, [isFinish])


    
    switch (isMenuActive) {
        case true:

            return <Suspense fallback={<Preloader />}>
                <Header />
                <EventItemPage />
            </Suspense>

        case false:

            return <Suspense fallback={<Preloader />}>
                <EventListPage />
            </Suspense>
        default:
            break;
    }

  
    // if (!isFinishComponent) {

    // return isFetched && <>
    //     {!isMenuActive
    //         ? <EventListContainer />
    //         : <EventItemContainer />
    //     }

    //     <PostSaleQuestionContainer />
    // </>


    // } else {
    //     return <CallingFinish result={result} setFinishStatus={setFinishStatus} />
    // }



}

