import { EV_PLAN_PROP } from "@/entities/EventPlan/type/event-plan-type";
import { EV_REPORT_PROP, EventReportSelectItem } from "@/entities/EventReport/type/event-report-type";
import { FC } from "react";
import ALabel from "../Label/ALabel";
import * as style from '../Input.module.scss'


interface ASelectProps {
    label: string,
    nameForHandler: EV_REPORT_PROP | EV_PLAN_PROP,
    handleChange: (
        type: EV_REPORT_PROP | EV_PLAN_PROP,
        value: string

    ) => void,
    current: EventReportSelectItem,
    items: Array<EventReportSelectItem>
}


const ASelect: FC<ASelectProps> = ({
    label,
    nameForHandler,
    handleChange,
    current,
    items

}) => {
    const id = `input-${label}-select-${nameForHandler}`;
    return (
        <>
            <ALabel
                htmlId={id}
                label={label}
                errorMessage={null}

            />
            <div className="col-md-12 mt-1  mb-1">
                <select className={`${style.input} form-control mt-0`}
                    value={current.code}
                    onChange={(e) => handleChange(nameForHandler, e.target.value)}
                >
                    {items && items.length &&
                        items.map(item => (
                            <option key={`${nameForHandler}-${item.id}`} value={item.code} >{item.name}</option>
                        ))}

                </select>
            </div>

        </>
    );
}

export default ASelect;