import React, { FC } from 'react';
import { Button } from 'reactstrap';
import * as classes from './Header.module.scss';
import { useAppDispatch } from '@/app/lib/hooks/redux';
import { send } from '@/processes/event';

export const Header: FC = ({ }) => {

    const dispatch = useAppDispatch();
    const sendEvent = () => { dispatch(send()) }

    return (
        <div className={classes.header}>
            <div className={classes.buttons}>
                <Button className={classes.btn_send} size='sm'
                    onClick={sendEvent}
                >
                    
                </Button>
                <Button className={classes.btn_cancel} size='sm'
                // onClick={cancelResultMenu}
                >
                    
                </Button>

            </div>
        </div>
    );
}

