import { FC, useState } from "react"
import { EV_REPORT_PROP, EventReportStateReport } from "../type/event-report-type"
import Noresult from "./components/Noresult/Noresult"
import Result from "./components/Result/Result"


interface ReportProps {
    report: EventReportStateReport,
    isNoResult: boolean,
    handleChange: (type: EV_REPORT_PROP, value: string) => void
}


const Report: FC<ReportProps> = ({
    report,
    isNoResult,
    handleChange


}) => {


    return !isNoResult ? <Result
        report={report}
        handleChange={handleChange}

    />
        : <Noresult
            report={report}
            handleChange={handleChange}
        />
}

export default Report