import { FC } from "react";
import { EV_PLAN_PROP } from "@/entities/EventPlan/type/event-plan-type";
import ALabel from "../Label/ALabel";
import * as style from '../Input.module.scss'


interface AInputProps {
    label: string | null,
    errorMessage: string | null,
    nameForHandler: EV_PLAN_PROP.NAME,
    handleChange: (
        type: EV_PLAN_PROP, value: string
    ) => void,
    handleOnFocus: () => void

}


const AInput: FC<AInputProps> = ({
    label,

    errorMessage,
    nameForHandler,
    handleChange,
    handleOnFocus,

}) => {
    const id = `input-${nameForHandler}`;
    return (
        <>
            {label && <ALabel
                htmlId={id}
                label={label}
                errorMessage={errorMessage}

            />}

            <div className="col-md-12 mt-2 mb-2">
                <input
                    onChange={
                        (e) => handleChange(
                            nameForHandler,
                            e.target.value
                        )}

                    onFocus={handleOnFocus}
                    className={`${style.input} mt-0 form-control ${errorMessage ? 'is-invalid' : ''}`}
                    // type="text"
                    // className={`form-control is-invalid`}
                    // defaultValue="2019-08-19"
                    id="example-string-input"
                    lang="rus"
                />
            </div>

        </>
    );
}

export default AInput;