import { FC } from "react";
import * as classes from './ALabel.module.scss'

interface ALabelProps {
    htmlId: string
    label: string | null,
    errorMessage?: string | null,

}


const ALabel: FC<ALabelProps> = ({
    htmlId,
    label,
    errorMessage,

}) => {

    return (<label
        htmlFor={htmlId}
        className={`col-md-12 col-form-label ${classes.label} ${errorMessage ? classes.label__error : ''}`}

    >
        {errorMessage
            ? errorMessage
            : label
        }
    </label>
    );
}

export default ALabel;