export const send = () => async (
    dispatch: AppDispatch, getState: AppGetState
) => {

    // https://april-hook.ru/api/task?company_id={{companyId}}&deadline={{  }}
    // &responsible={{}}&created=user_107&name={{  }}&crm={{ID}}
    const state = getState()





    // if (!type || !name || !responsibility || !createdBy || !deadline) {

    //     alert('   ')
    // } else {
    // const sale = state.postSale as PostSaleState

    // const questionStatus = sale.questionStatus
    // const isSaleComplete = sale.isSaleComplete

    // if (isSaleComplete && !questionStatus) {


    //     dispatch(postSaleAC.setSaleQuestionStatus(true))
    // } else {
    // dispatch(setPreloader(true))
    // dispatch(postSaleAC.setSaleQuestionStatus(false))
    const domain = state.app.domain
    const placement = state.app.bitrix.placement as Placement


    const event = state.event as EventState
    const reportState = state.eventReport as EventReportState
    const planState = state.eventPlan as EventPlanState
    const departament = state.department as DepartmentState
    const tasks = state.eventTask as EventTaskState
    const presentation = state.eventPresentation as PresentationState | null


   

    const description = reportState.report[EV_REPORT_PROP.COMMENT]
    const workStatus = reportState.report[EV_REPORT_PROP.WORK_STATUS]
    const noresultReason = reportState.report[EV_REPORT_PROP.NORESULT_REASON]
    const failType = reportState.report[EV_REPORT_PROP.FAIL_TYPE]
    const failReason = reportState.report[EV_REPORT_PROP.FAIL_REASON]
    const resultStatus = state.eventItemMenu.type
    const report = {
        resultStatus,
        description,
        workStatus,
        noresultReason,
        failType,
        failReason,
    }


    const responsibility = departament[DEPARTAMENT_STATE_PROP.PLAN][DUSER_ROLE.RESPONSIBLE].current
    const createdBy = departament[DEPARTAMENT_STATE_PROP.PLAN][DUSER_ROLE.RESPONSIBLE].current

    const type = planState[EV_PLAN_PROP.TYPE]
    const name = planState[EV_PLAN_PROP.NAME]
    const deadline = planState[EV_PLAN_PROP.DATE]
    const formattedDeadline = format(new Date(deadline), 'dd.MM.yyyy HH:mm:ss');
    const isPlanned = responsibility && createdBy && type && deadline && (
        workStatus.current.code === 'inJob' ||
        workStatus.current.code === 'setAside'
    )


    const plan = {
        responsibility,
        createdBy,
        type,
        name,
        deadline: formattedDeadline,
        isPlanned
    }


    const currentTask = tasks.current as EventTask | null


    //@ts-ignore

    // console.log('callingState.placement')
    // console.log(placement)
    // let companyId = null

    // const currentCompany = callingState[EVENT_PROP.COMPANY]
    // const currentSmart = callingState[EVENT_PROP.SMART]


    // if (placement) {
    //     if (placement.options) {
    //         companyId = placement.options.ID
    //     }
    // }
    const data = {
        domain,
        plan,
        report,
        placement,
        currentTask,
        presentation

    }
    // console.log('sending data')
    // console.log(data)
    // dispatch(
    //     eventTaskActions
    //         .setCurrentTask(null))


    const task = await hookAPI.service(
        'full',
        API_METHOD.POST,
        'result',
        data


    ) as any
    console.log('task')
    console.log(task)
    // let result = ' ' as ' ' | ' '
    // if (type.name === CallTypeName.PRESENTATION) {
    //     result = ' '
    // }

    // dispatch(callingAC.setFinishStatus(true, result))
    // dispatch(setPreloader(false))

    dispatch(eventTaskActions.setCurrentTask({ task: null }))
    dispatch(eventItemActions.setEventItemMenuStatus({
        status: false,
        menuType: null

    }))
}

