import React, { FC } from "react"
import * as classes from './EventItem.module.scss'
import { Col, Container, Row, } from "reactstrap"
import { ReportContainer } from "@/entities/EventReport"
import { EventItemResultType } from "../model/EventItemSlice"
import { Presentation } from "@/entities/EventPresentation"
import { Plan } from "@/entities/EventPlan"
import { Comment } from "@/entities/EventComment"
import 'bootstrap/dist/css/bootstrap.min.css';

interface EventItemProps {
    menuType: EventItemResultType,
    withPlan: boolean,
    // setCurentEventProp: (
    //     name: EVENT_PROPS,
    //     value: BXUser | null
    // ) => void

    //todo
    // send
    //cancelResultMenu
}




const EventItem: FC<EventItemProps> = ({
    menuType,
    withPlan,
    // setCurentEventProp,
    // send,
    // cancelResultMenu
}) => {


    return <React.Fragment>
       
        <Container fluid={true} className={classes.wrapper}>

            <Row className={`${classes.topArea} p-0`}>

                <Col sm={6} className="p-0">
                    <ReportContainer
                        isNoResult={false}
                    />
                </Col>
                <Col sm={6} className="p-0">
                    {menuType === 'result'
                        ? <Presentation />
                        : <ReportContainer isNoResult={true} />
                    }
                </Col>

            </Row>
            <Row className={`${classes.bottomArea}`}>
                <Col sm={6} className="p-0">
                    <Comment />

                </Col >
                {withPlan &&
                    <Col sm={6} className="p-0">
                        <Plan />
                    </Col>
                }
            </Row>
        </Container>



        {/* </div> */}
    </React.Fragment >
}

export default EventItem