import { FC, useEffect, useState } from "react"
import * as classes from './Plan.module.scss'
import { EVENT_PROP } from "@/processes/event/types/event-types"
import { EV_PLAN_PROP } from "../type/event-plan-type"
import { EV_PLAN_STATE_ITEM } from "../model/EventPlanSlice"
import { BXUser } from "@/app/types/bitrix/bitrix-type"
import ASelect from "@/shared/ui/Inputs/Select/ASelect"
import { DUserRoleStateItem } from "@/features/Departament/type/department-type"
import EVCard from "@/shared/ui/Cards/EventEntityCard/EventCard"
import { Col } from "reactstrap"
import AInput from "@/shared/ui/Inputs/Input/AInput"
import ADate from "@/shared/ui/Inputs/Date/ADate"


interface PlanProp {
    // department: DepartmentRoleStateItem
    responsibile: DUserRoleStateItem
    createdBy: DUserRoleStateItem
    date: string
    name: string
    type: EV_PLAN_STATE_ITEM
    eventPropChange: (type: EVENT_PROP, value: BXUser) => void
    planPropChange: (type: EV_PLAN_PROP, value: string) => void

}

const Plan: FC<PlanProp> = ({
    responsibile,
    createdBy,
    date,
    name,
    type,
    eventPropChange,
    planPropChange,
}) => {

    // const [currentPlan, setCurrentPlan] = useState(plan)
    // const [currentRespons, setCurrentRespons] = useState(responsibile?.current)
    // const [currentCreated, setCurrentCreated] = useState(createdBy?.current)
    const [isRequiredError, setIsRequiredError] = useState(false)
    const [nameErrorMessge, setIsNameErrorMessge] = useState('')


    useEffect(() => {
        if (isRequiredError) {
            setIsNameErrorMessge('   -  ')
        } else {
            setIsNameErrorMessge('')

        }
    }, [isRequiredError])

    const handleOnNameFocus = () => {
        if (isRequiredError) {

            setIsRequiredError(false)
        }


    }


    //  <React.Fragment>
    return <EVCard title={''} width={12}>
        <Col sm={12}>
            <AInput
                label={' -   '}
                errorMessage={nameErrorMessge}
                nameForHandler={EV_PLAN_PROP.NAME}
                handleChange={planPropChange}
                handleOnFocus={handleOnNameFocus}
            />
            <ADate
                label={'   '}
                value={date}
                // errorMessage={null}
                nameForHandler={EV_PLAN_PROP.DATE}
                handleChange={planPropChange}
                handleOnFocus={handleOnNameFocus}
            />
            <ASelect
                label={' '}
                nameForHandler={EV_PLAN_PROP.TYPE}
                handleChange={planPropChange}
                current={type.current}
                items={type.items}
            />
        </Col>
    </EVCard>


}

export default Plan