import { useAppSelector } from '@/app/lib/hooks/redux'
import { FC } from 'react'
import Gradient from '../Backgrounds/Gradient'
import { ROUTE } from '@/processes/routes/types/router-type'
import { RouterState } from '@/processes/routes/model/RouterSlice'



const Preloader: FC<{}> = () => {
    const router = useAppSelector(state => state.router) as RouterState
    const currentRoute = router.current.route as ROUTE


    return (
        // <GradientContainer
        //     isComponent={false}
        //     currentRoute={currentRoute}
        //     isActive={true}>
        //     <Logo />
        // </GradientContainer>    
        <Gradient
            isComponent={false}
            currentRoute={currentRoute}
            isActive={true}
        />

    )
}
export default Preloader