import Report from "./Report"
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { EventItemResultType } from "@/widgets/EventItem/model/EventItemSlice"
import { EV_REPORT_PROP, EventReportStateReport } from "../type/event-report-type"
import { eventReportActions } from "../model/EventReportSlice"
import { FC } from "react"

interface ReportContainerProps {
    isNoResult: boolean
}
export const ReportContainer: FC<ReportContainerProps> = ({
    // setCurentCallingProp,
    isNoResult

}) => {

    const dispatch = useAppDispatch();
    const reportState = useAppSelector(state => state.eventReport)
    const eventItemState = useAppSelector(state => state.eventItemMenu)

    const report = reportState.report as EventReportStateReport




    const setEventProp = () => {
        dispatch(
            setEventProp
        )
    }

    const handleChange = (type: EV_REPORT_PROP, value: string) => {

        dispatch(
            eventReportActions.setReportProp({
                propName: type,
                value
            })
        )
        // if (
        //     type === EV_REPORT_PROP.FAIL_REASON ||
        //     type === EV_REPORT_PROP.COMMENT ||
        //     type === EV_REPORT_PROP.FAIL_TYPE ||
        //     type === EV_REPORT_PROP.NORESULT_REASON ||
        //     type === EV_REPORT_PROP.WORK_STATUS
        // ) {


        //     setReportProp(type, value)
        // } else {

        //     setCurentEventProp(type, value)
        // }

    }

    return <Report
        report={report}
        isNoResult={isNoResult}
        handleChange={handleChange}
    />


}

