import { DEV_CURRENT_USER_ID, TESTING_COMPANY_ID, TESTING_DOMAIN } from "@/app/consts/app-global"
import { AppDispatch, AppGetState } from "@/app/model/store"
import { onlineGeneralAPI } from "@/app/services/april-online-api"
import { bitrixAPI } from "@/app/services/bitrix-general-api"
import { API_METHOD } from "@/app/types/api/api-type"
import { BXTask } from "@/app/types/bitrix/bitrix-type"
import { Placement } from "@/app/types/bitrix/placement-type"
import { EventTask } from "../types/event-task-type"

import { parseISO, format } from "date-fns"
import { ru } from "date-fns/locale/ru";
import { Portal } from "@/app/types/portal/portal-type"
import { eventTaskActions } from "./EventTaskSlice"

//thunks


export const initialEventTasks = () => async (
    dispatch: AppDispatch, getState: AppGetState
) => {
    // dispatch(setPreloader(true))
    
    const state = getState()
    const domain = state.app.domain
    const currentUser = state.app.bitrix.user
    let placement = state.app.bitrix.placement as Placement | undefined

    const data = {
        domain: domain || TESTING_DOMAIN,
        userId: currentUser || DEV_CURRENT_USER_ID,
        placement: placement || {
            placement: 'COMPANY',
            options: {
                ID: TESTING_COMPANY_ID
            }
        },

    }
    
    const tasks = await onlineGeneralAPI
        .service(
            'bitrix/callingreport',
            API_METHOD.POST,
            'tasks',
            data
        ) as Array<BXTask> | null



    const getPortaldata = {
        domain: domain || TESTING_DOMAIN,
        userId: currentUser || DEV_CURRENT_USER_ID,

    }
    const portal = await onlineGeneralAPI
        .service(
            'front/portal',
            API_METHOD.POST,
            'portal',
            getPortaldata
        ) as Portal | null
        


    // IS_PROD && portal && dispatch(getHistory(portal))

    if (tasks && tasks.length) {
        const resultTasks = tasks.map(task => {

            const { type, name, eventType } = parseTaskTitle(task.title)
            const isExpired = checkIfTaskIsOverdue(task)
            const deadline = getFormatDate(task.deadline)
            return {
                ...task,
                name,
                type,
                eventType,
                isExpired,
                deadline
            } as EventTask
        }) as Array<EventTask>

        
        dispatch(
            eventTaskActions
                .setFetchedTasks({ tasks: resultTasks }))
    }



}




//utils
const checkIfTaskIsOverdue = (task: BXTask): 'no' | 'almost' | 'yes' => {
    let result = 'no' as 'no' | 'almost' | 'yes'
    const now = new Date(); //  
    const deadline = new Date(task.deadline); //   deadline   Date
    if (deadline < now) {
        result = 'yes'
    } else {
        deadline.setHours(0, 0, 0, 0); //  ,   
        now.setHours(0, 0, 0, 0);
        const isNearlyOverdue = now.getTime() === deadline.getTime();

        if (isNearlyOverdue) {
            result = 'almost'

        }

    }
    return result;
}


const parseTaskTitle = (title: string) => {
    const typePattern = /^(.*?)(?=\s)/;
    const namePattern = /[]?\s+(.*?)\s+\d{2}\.\d{2}\.\d{4}/;

    const typeMatch = title.match(typePattern);
    const nameMatch = title.match(namePattern);

    const type = ''
    // typeMatch ? typeMatch[1] : null; //   
    let eventType = 'xo'
    const phrases = [
        " ",
        " ",
        "",
        "",
        "  ",
        "  "
    ];
    const regex = new RegExp(phrases.join("|"), "i");
    const nameResult = title.match(regex);
    const name = nameResult ? nameResult[0] : title;

    if (name == phrases[0] || name == phrases[1]) {
        eventType = 'xo'
    } else if (name == phrases[2]) {
        eventType = 'warm'
    } else if (name == phrases[3]) {
        eventType = 'presentation'
    } else if (name == phrases[4]) {
        eventType = 'in_progress'
    } else if (name == phrases[5]) {
        eventType = 'money_await'
    } else {
        eventType = 'event'
    }
    
    // const name = title
    // nameMatch ? nameMatch[1] : null; //   

    return { type, name, eventType };
}

const getFormatDate = (date: string) => {
    if (date) {
        const parsedDate = parseISO(date);

        //      
        const formattedDate = format(parsedDate, 'd MMMM yyyy HH:mm', { locale: ru });
        return formattedDate
    }
    return ''

}
